const DEFAULT = {
  width: 1,
  widthUnit: '',
  isFlexGrow: true
};

export class Column {
  constructor() {
    Object.assign(this, DEFAULT);
  }
}



const UPDATE_WIDTH = 'UPDATE';

// action creator
const updateText = (number) => {
  return {
    type: UPDATE_WIDTH,
    number
  };
};

// reducer{
function textUpdater(state = { ...DEFAULT }, action) {
  switch (action.type) {
  case UPDATE_WIDTH:
    return {
      raw: action.text,
      html: marked(action.text)
    };
  default:
    return state;
  }
}
